.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.layout {
  background-color: #e0ebf5;
  height: 100vh;
}

.errorLayout {
  background-color: #e0ebf5;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.modalTitle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: #183a59;
  font-weight: 700 !important;
  text-align: center;
}

.modalActions {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  padding-top: 5%;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}